import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue';
import uniSwiperDot from '@/components/uni-swiper-dot/uni-swiper-dot.vue';
import {mapState} from 'vuex';  

export default {
	components: {
		uniNoticeBar,uniSwiperDot
	},
	computed: {
		...mapState(['hasLogin','userInfo'])
	},
	data() {
		return {
			dotsStyles: {
				backgroundColor: '#DBDBDB',
				border: '1px #DBDBDB solid',
				color: '#fff',
				selectedBackgroundColor: '#00A390',
				selectedBorder: '1px #00A390 solid'
			},
			
			//高度 3个： 760,2: 520, 1:  140 rpx
			tgFrameHeight: 'height: 520rpx !important;',
			
			carouselList: [], //头部广告数据
			goodsList: [], //
			
			//团购
			info: [{},{},{}],
			tgCurrent: 0,
			avatar: [
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big81005.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big25002.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big91012.jpg'
			],
			
			//爆款
			bkInfo: [{},{},{}],
			bkCurrent: 0,	
				
			
			//头部菜单
			isClickChange: false,
			TabCur: 0,
			scrollLeft: 0,
			newsitems: [], //每页加载的数据
			tabBars: [{
				name: '推荐',
				id: 0
			}, {
				name: '女装服饰',
				id: 1
			}, {
				name: '男装服饰',
				id: 10
			}, {
				name: '电脑办公',
				id: 3
			}, {
				name: '财经',
				id: 4
			}, {
				name: '娱乐',
				id: 5
			}, {
				name: '军事',
				id: 6
			}, {
				name: '历史',
				id: 7
			}, {
				name: '本地',
				id: 8
			}]
		};
	},

	onLoad() {
		this.loadData();
	},
	methods: {
		/**
		 * 请求静态数据只是为了代码不那么乱
		 * 分次请求未作整合
		 */
		async loadData() {
			let carouselList = await this.$api.json('carouselList');
			this.carouselList = carouselList;
			
			let goodsList = await this.$api.json('goodsList');
			this.goodsList = goodsList || [];
		},
		navTo(url){
			if(!this.hasLogin){
				url = '/pages/public/login';
			}
			uni.navigateTo({  
				url
			})  
		},
		//详情页
		navToDetailPage(item) {
			//测试数据没有写id，用title代替
			let id = item.title;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		navToCategory(){
			uni.switchTab({
				url: `/pages/category/category`
			})
		},
		hqmsclick(){
			var that = this;
			uni.showToast({
			  title: '点击菜单',
			  icon: 'success',
			  duration: 2000
			})
		},
		//团购切换
		async tgSwitch(e){
			this.tgCurrent = e.detail.current
		},
		//爆款
		async bkSwitch(e){
			this.bkCurrent = e.detail.current
		},
		// 头部菜单切换
		async tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			this.isClickChange = true;
			
			// if(this.newsitems[tabIndex].data.length === 0){ //加载数据
			// 	this.addData(tabIndex)
			// }
		},
		getnewsMore(){
			this.$api.msg('get news more');
			this.navTo('/pages/article/list');
		},
		async changeTab(e) {
			let index = e.target.current;
			
			// if(this.newsitems[index].data.length === 0){ //加载商品数据数据
			// 	this.addData(index)
			// }
			if (this.isClickChange) {
				this.TabCur = index;
				this.isClickChange = false;
				return;
			}
			
			this.scrollLeft = (index - 1) * 60;
			this.isClickChange = false;
			this.TabCur = index;
		}
	},

























	onShareAppMessage() { //设置分享
		return {
			title: '欢迎体验uni-app',
			path: '/pages/index/index'
		}
	}
}
